export default class touchTest extends cc.EventTarget {
    private _collider: fgui.GObject;

    public constructor(mainView: fgui.GComponent) {
        super();

        this._collider = mainView.getChild("n16");
        console.log(this._collider);
        this._collider.draggable = true;
        
        this._collider.on(fgui.Event.TOUCH_BEGIN, this.touchStart, this);
        this._collider.on(fgui.Event.TOUCH_MOVE, this.touchMove, this);
    }

    private touchStart(evt: fgui.Event) {
        console.log('touchStart');
        console.log(evt)
    }

    private touchMove(evt: fgui.Event) {
        console.log('touchMove');
        console.log(evt)
    }

}
